<template>
 <section>
  <el-header class = 'header' style="background:#EDF2FC;">
   <el-breadcrumb separator-class="el-icon-arrow-right" class="breadcrumb">
      <el-breadcrumb-item style="line-height:40px;">请假列表</el-breadcrumb-item>
    </el-breadcrumb>
 </el-header>
<!--工具条-->
<el-main>
<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
    <el-form :inline="true" :model="searchCriteriaForm">
        <el-form-item>
            <el-input v-model="searchCriteriaForm.keyword" placeholder="姓名"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" v-on:click="getUsersed">查询</el-button>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" v-on:click="getUsersAll">全部</el-button>
        </el-form-item>
    </el-form>
</el-col>
 <el-table :data="users" highlight-current-row v-loading="listLoading" style="width: 100%;" border cell-class-name='table_style' header-row-class-name='header_style'>
    <el-table-column type="expand">
      <template slot-scope="props">
        <div style="height: 200px;" @click='getProps(props.row)'>
        <el-steps direction="vertical" :active="props.row.AuditStaffLevelCurrents">
          <el-step title="步骤 1">
            <template slot="description">
            <span>{{ '审核状态:  ' +  (props.row.renderStatus1.title) +'&nbsp;&nbsp;&nbsp;审核人:  '+ props.row.AuditStaff1.Name}}</span>
            <span>审核时间: <span>{{props.row.AuditDate1}}</span></span>
            <button v-if="props.row.renderStatus1.isShowButton" @click="getStationLeave(props.row)">审批</button>
            </template>
          </el-step>
          <el-step title="步骤 2" v-if='props.row.AuditStaffLevelMax>1'>
            <template slot="description">
            <span>{{ '审核状态:  ' + (props.row.renderStatus2.title)+'&nbsp;&nbsp;&nbsp;审核人:  '+ props.row.AuditStaff2.Name}}</span>
            <span>审核时间: <span>{{props.row.AuditDate2}}</span></span>
            <button v-if="props.row.renderStatus2.isShowButton" @click="getStationLeave(props.row)">审批</button>
            </template>
          </el-step>
          <el-step title="步骤 3" v-if='props.row.AuditStaffLevelMax>2'>
           <template slot="description">
            <span>{{ '审核状态:  ' + (props.row.renderStatus3.title)+'&nbsp;&nbsp;&nbsp;审核人:  '+ props.row.AuditStaff3.Name}}</span>
            <span>审核时间: <span>{{props.row.AuditDate3}}</span></span>
            <button v-if="props.row.renderStatus3.isShowButton" @click="getStationLeave(props.row)">审批</button>
          </template>
          </el-step>
           <el-step title="步骤 4" v-if='props.row.AuditStaffLevelMax>3'>
           <template slot="description">
            <span>{{ '审核状态:  ' + (props.row.renderStatus4.title)+'&nbsp;&nbsp;&nbsp;审核人:  '+ props.row.AuditStaff4.Name}}</span>
            <span>审核时间: <span>{{props.row.AuditDate4}}</span></span>
            <button v-if="props.row.renderStatus4.isShowButton" @click="getStationLeave(props.row)">审批</button>
          </template>
          </el-step>
       </el-steps>
      </div>
      </template>
    </el-table-column>
    <el-table-column prop="TargetStaff.Name" label="姓名" :span='2'>
		</el-table-column>
    <el-table-column prop="StaffLeaveType.Name" label="请假原因" :span='2'>
		</el-table-column>
    <el-table-column label="开始时间" :span='3'>
      <template slot-scope="props">
        {{props.row.BeginDate}}&nbsp;&nbsp;{{props.row.BeginDatePartial}}
      </template>
		</el-table-column>
     <el-table-column label="结束时间" :span='3'>
      <template slot-scope="props">
        {{props.row.EndDate}}&nbsp;&nbsp;{{props.row.EndDatePartial}}
      </template>
		</el-table-column>
		</el-table-column>
    <el-table-column prop="days" label="请假天数" :span='2'>
		</el-table-column>
     <el-table-column prop="RequestStaff.Name" label="申请人" :span='3'>
		</el-table-column>
     <el-table-column prop="RequestAttachmentURL1" label="申请附件1" :span='3'>
		</el-table-column>
     <el-table-column prop="RequestAttachmentURL2" label="申请附件2":span='3'>
		</el-table-column>
    <el-table-column label="操作"  width= '200' fixed="right">
			<template slot-scope="scope">
        <el-button size="small" @click="getStationLeave(scope.row)" v-if="scope.row.isStationLeave">审批</el-button>
			</template>
		</el-table-column>
  </el-table>
  	<!--工具条-->
		<el-col :span="24" class="toolbar">
		<el-pagination
      @size-change="handlePaginationSizeChange"
      @current-change="handlePaginationCurrentChange"
      :current-page="pagingInfoForm.pageNumber"
      :page-sizes="[10, 50, 100, 200, 400]"
      :page-size="pagingInfoForm.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="page.totalItemCount"
      style="float:right;"
      v-if="page.totalItemCount"
      >
    </el-pagination>
		</el-col>
  <!-- 审核页面 -->
  <el-dialog :title="userEntry" :visible.sync="isAuditStationLeave" :close-on-click-modal="false" class="entry_style">
    <el-form class="small-space" :model="AuditStationLeaveForm" ref="AuditStationLeaveForm" label-position="right" label-width="120px" size="mini" style='width: 500px; margin-left:50px;' v-loading="isloading">
       <el-form-item label="原因">
        <el-input v-model="AuditStationLeaveForm.AuditRemark" class="input_style"></el-input>
      </el-form-item>
    </el-form>
    <div  class="submit"> 
      <div class="buttonStyle">
        <el-button size="small" @click="getAuditing">同意</el-button>
        <el-button size="small" type="danger" @click="getUnAuditing">拒绝</el-button>
     </div>   
    </div> 
  </el-dialog>
  </el-main>
 </section>   
</template>
<script>
  import api from '@/utils/api'
  import _ from 'lodash'
  export default {
    data () {
      return {
        users: [],
        filters: {
          Keyword: ''
        },
        listLoading: false,
        isAuditStationLeave: false,
        AuditStationLeaveForm: {
          StaffLeaveID: '',
          AuditStatus: '',
          AuditRemark: ''
        },
        isloading: false,
        userEntry: '',
        pagingInfoForm: {
          pageNumber: 1,
          pageSize: 10,
          isExcludeMetaData: false,
          sortInfo: {
            sort: 'StaffLeaveID',
            sortDir: 'DESC'
          }
        },
        searchCriteriaForm: {
          keyword: null,
          groupIDs: [],
          roleIDs: [],
          StaffStatusIDs: [],
          groupIDPath: []
        },
        page: {
          list: null,
          totalItemCount: null,
          totalPageCount: null
        }
      }
    },
    mounted: function () {
      this.getStationLeaveList()
    },
    methods: {
      getRenderrStatus (currentRenderrLevel, auditStatus, CurrentUserAudituditStaffLevel) {
        var temp = currentRenderrLevel * 2
        if (auditStatus < temp - 1) {
          // 未审
          return {
            title: '未审',
            isShowButton: currentRenderrLevel === CurrentUserAudituditStaffLevel && (auditStatus === (currentRenderrLevel - 1) * 2 - 1)
          }
        } else if (auditStatus === temp) {
          // 本层拒绝
          return {
            title: '拒绝',
            isShowButton: false
          }
        } else {
          // 本层通过
          return {
            title: '通过',
            isShowButton: false
          }
        }
      },
      getProps (e) {
        console.log('数据' + JSON.stringify(e))
      },
      // 分页
      handlePaginationSizeChange (val) {
        this.pagingInfoForm.pageSize = val
        this.pagingInfoForm.pageNumber = 1
        this.getStationLeaveList()
      },
      handlePaginationCurrentChange (val) {
        this.pagingInfoForm.pageNumber = val
        this.getStationLeaveList()
      },
      // 分页结束
      getStationLeaveList () {
        const params = _.extend({}, this.pagingInfoForm, this.searchCriteriaForm)
        api.getStationLeaveList(params).then(response => {
          this.page = response.data.page
          const list = response.data.page.list
          console.log('分页数据', JSON.stringify(response))
          for (let i = 0; i < list.length; i++) {
            list[i].BeginDate = list[i].BeginDate.substr(0, 11)
            list[i].EndDate = list[i].EndDate.substr(0, 11)
            list[i].days = parseInt(list[i].HalfDays) / 2
            if (list[i].BeginDatePartial === 1) {
              list[i].BeginDatePartial = '上午'
            } else {
              list[i].BeginDatePartial = '下午'
            }
            if (list[i].EndDatePartial === 1) {
              list[i].EndDatePartial = '上午'
            } else {
              list[i].EndDatePartial = '下午'
            }
            // 判断审核层级
            list[i].AuditStaffLevelCurrents = list[i].AuditStaffLevelCurrent + 1
            for (let j = 1; j <= list[i].AuditStaffLevelMax; j++) {
              list[i]['renderStatus' + j] = this.getRenderrStatus(j, list[i].AuditStatus.AuditStatusID, list[i].CurrentUserAudituditStaffLevel)
              if (j === list[i].CurrentUserAudituditStaffLevel) {
                list[i].isStationLeave = list[i]['renderStatus' + j].isShowButton
              }
              console.log(j, list[i].AuditStaffLevelMax, list[i].AuditStatus.AuditStatusID, list[i]['renderStatus' + j])
            }
          }
          this.users = list
          console.log('请假列表' + JSON.stringify(response.data.list))
        }, error => {
          this.$message({
            message: error.message,
            type: 'error'
          })
        })
      },
      // 审核
      getStationLeave (scope) {
        this.isAuditStationLeave = true
        this.userEntry = scope.TargetStaff.Name + ' 请假审批'
        this.AuditStationLeaveForm.StaffLeaveID = scope.StaffLeaveID
        console.log('审核' + JSON.stringify(scope))
      },
      // 同意
      getAuditing () {
        this.AuditStationLeaveForm.AuditStatus = 1
        this.isloading = true
        const StationLeaveForm = []
        StationLeaveForm.push(this.AuditStationLeaveForm)
        api.getAuditStationLeave(StationLeaveForm).then(response => {
          this.isloading = false
          this.isAuditStationLeave = false
          this.getStationLeaveList()
          console.log('传输成功', JSON.stringify(response))
        }, error => {
          this.$message({
            message: error.message,
            type: 'error'
          })
          this.isloading = false
        })
        console.log('同意传输数据', this.AuditStationLeaveForm)
      },
      // 拒绝
      getUnAuditing () {
        this.AuditStationLeaveForm.AuditStatus = 2
        this.isloading = true
        const StationLeaveForm = [this.AuditStationLeaveForm]
        api.getAuditStationLeave(StationLeaveForm).then(response => {
          this.isloading = false
          this.isAuditStationLeave = false
          this.getStationLeaveList()
          console.log('拒绝成功', JSON.stringify(response))
        }, error => {
          this.$message({
            message: error.message,
            type: 'error'
          })
          this.isloading = false
        })
        console.log('拒绝传输数据', this.AuditStationLeaveForm)
      },
      // 查询
      getUsersed () {
        console.log('查询', this.searchCriteriaForm)
        this.getStationLeaveList()
      },
      // 全部
      getUsersAll () {
        this.resetsearchForm()
        this.getStationLeaveList()
      },
      // 清空搜索条件
      resetsearchForm () {
        this.searchCriteriaForm = {
          keyword: null,
          groupID: null,
          roleID: null,
          groupIDPath: []
        }
      }
    }
  }
</script>
<style scodived lang="scss">
.el_dialogs>div{
  min-width:640px;
  width: 30%;
}
.dialog_style{
  min-width:1000px;
}
.dropdown_style{
  position: relative;
  width: 192px;
}
.entry_styles>div{
  min-width:540px;
  width: 28%;
}
.entry_style>div{
    min-width:600px;
    width: 30%;
}
.table_style{
  text-align: center;
}
.el-table th{
   text-align: center;
}
.el-table td{
  padding: 5px 0;
}
.dropdown_children{
  width: 146px;
  height: 100%;
  margin-left: 15px;
}
.userdButton{
  margin-top:10px; 
}
.input_style{
  width: 200px;
}
.optionStyle{
 height:300px;
 padding:0;
 background-color: #ffffff 
}
.p_Style{
  width: 100%;
  height: 100%;
  line-height: 100%;
}
.el-dropdown-menu{
  min-width: 200px;
  max-height: 300px;
  padding: 10px 10px;
  overflow-y: scroll;
}
.el_icon{
  position: absolute;
  width: 30px;
  height:27px;
  top: 1px;
  right:2px;
  text-align: center;
  line-height: 27px;
  color: #b4bccc;
}
.buttonStyle{
  width: 200px;
  height:100%;
  position: absolute;
  top:0;
  left: 50%;
  margin-left: -100px;
}
.submit{
  position: relative;
  width: 100%;
  height: 50px;
}
.el-dialog__body{
 padding: 30px 20px;
}
</style>


